.main {
  width: 100%;
  border: 1px solid green;
}


.map {
  border: 1px solid red;
  position: relative;
  max-width: 60%;
}

img {
  width: 100%;
}


/*Information block start*/

.information {
  position: absolute;
  width: 300px;
  background-color: #fff;
  z-index:6;
  border: 10px solid rgb(166, 155, 195);
  border-radius: 25px;
  top: 150px; 
  padding: 15px;
}

.infoClose {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 50px;
  height: 50px;
}

.infoClose span {
  display: block;
  position: absolute;
  border: 3px solid rgb(166, 155, 195);
  width: 25px;
}

.infoClose:hover span {
  border: 3px solid red;
}

.infoClose span:first-child{
  transform: rotate(45deg);
  margin: 20px 10px;
}

.infoClose span:last-child{
  transform: rotate(-45deg);
  margin: 20px 10px;
}

.infoTextBlock{
  margin-top: 50px;
}

.infoTitle {
  text-align: center;
}

.infoTrans {
  text-align: center;
}

.infoNote, .infoPlus{
  text-align: justify;
  text-indent: 35px;
}

/*information block end*/

/*Style of points start*/

.villagesSpan{
  display: block;
  position: absolute;
  border: 10px solid black;
  border: black 10px solid;
  border-radius: 50%;
  z-index: 4;
  width: 4%;
  height: 5%;
}

.villagesSpan:hover {
  width: 60px;
  height: 60px;
  border: none;
  background-color: blue;
  z-index: 6;
}

.villagesSpan:active {
  width: 60px;
  height: 60px;
  background-color: orange;
}

.yellow {
  border: none;
  background-color: yellow;
  z-index: 5;
}

.red {
  border: none;
  background-color: red;
  z-index: 5;
}

.greenRed {
  background-color: red;
  z-index: 5;
  border: 10px green solid;
  box-sizing: border-box;
}

.greenYellow{
  background-color: yellow;
  z-index: 5;
  border: 10px green solid;
  box-sizing: border-box;
}

/*Style of points end*/
